import React from 'react'
import {useLocation} from 'react-router-dom'
import { useParams } from 'react-router'
import { useSelector, useDispatch } from 'react-redux'
import { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { getInfos } from '../../redux/pokemons/infosReducer'
import { unsetInfos } from '../../redux/pokemons/infosReducer'
import {v4 as uuidv4} from 'uuid'

export default function Evolution() {

    const location = useLocation();

    const params = useParams();

    const {infos} = useSelector(state => ({
        ...state.infosReducer
    }))

    const dispatch = useDispatch()
    
    useEffect(() => {
        if (infos.length === 0 || infos.name !== params.slug) {
            dispatch(getInfos(params.slug))
        }

        return () => {
            unsetInfos()
        }
    })

    return (
        <div className="infos-pokemon">
                <h1>{infos.length !== 0 && infos.name}</h1>
                <img className="pokemon-img" src={infos.length !== 0 ? infos.sprites.other["official-artwork"].front_default : "wait" } alt={infos.length !== 0 ? infos.name : "wait"} />
                <div className="pokemon-size">
                    <div className="pokemon-types">
                        <p>Type(s) :</p>
                        <ul>
                            {infos.length !== 0 &&  infos.types.map(item => {
                                return(
                                    <li key={uuidv4()} className="pokemon-type">{infos.length !== 0 && item.type.name}</li> 
                                    )
                                })}
                            </ul>
                    </div>
                    <p>
                        Weight : {infos.length !== 0 && infos.weight/10} kg
                        <br/>
                        Height : {infos.length !== 0 && infos.height/10} m
                    </p>
                    <Link 
                        to = {"/Infos/" + location.state}
                        state = {location.state}
                    >
                        Return to previous state : {location.state}
                    </Link>
                </div>
        </div>
    )
}
