const INITIAL_STATE = {
    infos: []
}

function infosReducer(state = INITIAL_STATE, action){
    switch (action.type) {
        case "LOADINFOS":
            return{
                infos: action.payload
            }
        case "UNSETINFOS":
            return{
                infos: []
            }
    
        default:
            return state;
    }
}

export default infosReducer

export const getInfos = (name) => dispatch => {
    fetch("https://pokeapi.co/api/v2/pokemon/" + name)
    .then(response => response.json())
    .then(data => {
        dispatch({
            type: "LOADINFOS",
            payload: data
        })
    })
}

export const unsetInfos = () => dispatch => {
    dispatch({
        type: "UNSETINFOS",
    })
}