import React from 'react'
import './Infos.css'
import { useParams } from 'react-router'
import { useSelector, useDispatch } from 'react-redux'
import { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { getInfos } from '../../redux/pokemons/infosReducer'
import { unsetInfos } from '../../redux/pokemons/infosReducer'
import { getEvolution } from '../../redux/pokemons/evolutionReducer'
import { unsetEvolution } from '../../redux/pokemons/evolutionReducer'
import {v4 as uuidv4} from 'uuid'

export default function Infos() {

    const params = useParams();

    const {evolutions,infos} = useSelector(state => ({
        ...state.evolutionReducer,
        ...state.infosReducer
    }))

    const dispatch = useDispatch()
    
    useEffect(() => {
        if (infos.length === 0 || infos.name !== params.slug) {
            dispatch(getInfos(params.slug))
        }
        
        if ((!evolutions[0] && infos.length !== 0) || (infos.length !== 0 && evolutions[0] !== params.slug && evolutions[1] !== params.slug && evolutions[2] !== params.slug)) {
            dispatch(getEvolution(infos.species.url));
        }

        return () => {
            unsetEvolution()
            unsetInfos()
        }
    })
    
    return (
        <div className="infos-pokemon">
                <h1>{infos.length !== 0 && infos.name}</h1>
                <img className="pokemon-img" src={infos.length !== 0 ? infos.sprites.other["official-artwork"].front_default : "wait" } alt={infos.length !== 0 ? infos.name : "wait"} />
                <div className="pokemon-size">
                    <div className="pokemon-types">
                        <p>Type(s) :</p>
                        <ul>
                            {infos.length !== 0 &&  infos.types.map(item => {
                                return(
                                    <li key={uuidv4()} className="pokemon-type">{infos.length !== 0 && item.type.name}</li> 
                                    )
                                })}
                            </ul>
                    </div>
                    <p>
                        Weight : {infos.length !== 0 && infos.weight/10} kg
                        <br/>
                        Height : {infos.length !== 0 && infos.height/10} m
                    </p>
                </div>
                <div className="pokemon-evolution">
                    {evolutions.map(item => {
                            if (item !== params.slug) {
                                return(
                                <Link 
                                    key={uuidv4()} 
                                    to={"/Evolution/" + item}
                                    state={params.slug}
                                >
                                    Evolution  : {item}
                                </Link>
                                )
                            }
                        })}
                </div>
        </div>
    )
}
